import axios from "axios";

const auth_Endpoint = "https://todo-list.alphacamp.io/api/auth";

export async function login({username, password}){
  try {
    const { data } = await axios.post(`${auth_Endpoint}/login`, {
      username,
      password,
    });

    console.log(data);

    const { authToken } = data;
    //serverauthToken,success key. , data
    if (authToken) {
      return {
        success: true,
        ...data,
      }
    }
    return data;
  }
  catch(error){
    console.error(error)
  }
}

export async function signup({username , password, email}){
  try{
    const { data } = await axios.post(`${auth_Endpoint}/register`, { 
      username, 
      password , 
      email});

    //dataauthToken
    const { authToken } = data 

    if(authToken){
      return {
        success:true,
        ...data
      }
    }
    return {
      data
    }
  }
  catch(error){
    console.error(error)
  }
}

export async function checkPermission(authToken){
  if (!authToken) {
      console.error("No authToken provided for permission check");
      return null;
      }
  try {     
      const { data } = await axios.get(`${auth_Endpoint}/test-token`, {
        headers: {
          Authorization: `Bearer ${authToken}`,
        },
      });
    return data.success;
  } catch(error){
    console.error("Error response:", error.response?.data || error.message);
    return null
  }
}