import { createContext, useContext, useState } from "react"
//Context
// import jwt from 'jsonwebtoken'
import { jwtDecode } from 'jwt-decode'
import { useLocation } from "react-router-dom"
import { useEffect } from "react"
import { signup, login, checkPermission } from "../api/auth"

const defaultAuthContext = {
  isAuthenticated: false, //, false, ()true 
  currentMember: null, // , null, 
  signup: null, //
  login: null, //
  logout: null //
}

const AuthContext = createContext(defaultAuthContext)

//AuthProvider,<AuthContext.Provider>

//context hook 
export const useAuth = () => useContext(AuthContext);
export const AuthProvider = ({children}) => {
  const [isAuthenticated, setIsAuthenticated]=useState(false) //api
  const [payload, setPayload]=useState(null)//payloadapiauthToken

  //useEffect
  const {pathname} = useLocation()

    useEffect(() => {
      const CheckTokenIsValid = async() => {
        const authToken = localStorage.getItem("authToken");

        //authToken, , 
        if (!authToken) {
          console.log("authToken is not existed!")
          setIsAuthenticated(false)
          setPayload(null)
          return;
        }

        const isValid = await checkPermission(authToken); //  authToken 
        if (isValid) {
          setIsAuthenticated(true);
          const tempPayload = jwtDecode(authToken);
          setPayload(tempPayload);
        } else {
          setIsAuthenticated(false);
          setPayload(null);
          console.log("Invalid auth token");
        }
      }

      CheckTokenIsValid();
    }, [pathname]);


   //state,providervalue
  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        currentMember: payload && {
          id: payload.sub,
          name: payload.name,
        },
        //authContext,data
        signup: async (data) => {
          const { success, authToken } = await signup({
            username: data.username,
            email: data.email,
            password: data.password,
          });
          const tempPayload = jwtDecode(authToken);
          //payload, , 
          if (tempPayload) {
            setPayload(tempPayload);
            setIsAuthenticated(true); //
            localStorage.setItem("authToken", authToken);
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
          return success;
        },
        login: async (data) => {
          const { success, authToken } = await login({
            username: data.username,
            password: data.password,
          });

          if (authToken) {
            const tempPayload = jwtDecode(authToken);
            setPayload(tempPayload);
            setIsAuthenticated(true);
            localStorage.setItem("authToken", authToken); // 
          } else {
            setPayload(null);
            setIsAuthenticated(false);
          }
          return success;
        },
        logout: () => {
          localStorage.removeItem("authToken");
          setIsAuthenticated(false);
          setPayload(null);
        },
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

