import {
  AuthContainer,
  AuthInputContainer,
  AuthButton,
  AuthLinkText,
} from "../components/common/auth.styled";
import { AuthInput } from "../components";
import { ACLogoIcon } from "../assets/images";
import { useState, useEffect } from 'react'
import { Link,useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { useAuth } from "../contexts/AuthContext";

const LoginPage = () => {
  //stateonchange
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate()

  //AuthContext
  const { login, isAuthenticated } = useAuth();

  function handleAccountOnChange(event){
    setUsername(event.target.value);
    console.log(username)
  }
  function handlePassWordOnChange(event) {
    setPassword(event.target.value);
    console.log(password)
  }

  async function handleLoginClick(){
    if(username.length === 0){
      return
    }

    if(password.length === 0){
      return
    }
    
    const success = await login({username, password})
    console.log("AuthContent login Return success!")
    if (success){
      Swal.fire({
        title: "!",
        icon: "success",
        showConfirmButton: false,
        timer: 1000,
        position: "center"
      })
    } else {
      Swal.fire({
        title: "!",
        icon: "error",
        showConfirmButton: false,          
        timer: 1000,
         position: "center",
        });
      setUsername('') //
      setPassword('') //
    }
  }

  useEffect(() => {
    if (isAuthenticated) {
      navigate("/todo")
      console.log("isAuthenticated change or navigate triggered")
    }
  }, [navigate,isAuthenticated]);

  return (
    <AuthContainer>
      <div>
        <ACLogoIcon />
      </div>
      <h1> Todo</h1>

      <AuthInputContainer>
        <AuthInput
          label=""
          placeholder=""
          type="text"
          value={username}
          onChange={handleAccountOnChange}
        />
      </AuthInputContainer>

      <AuthInputContainer>
        <AuthInput
          label=""
          placeholder=""
          type="password"
          value={password}
          onChange={handlePassWordOnChange}
        />
      </AuthInputContainer>
      <AuthButton 
        onClick={handleLoginClick}></AuthButton>
      <Link to="/signup">
        <AuthLinkText></AuthLinkText>
      </Link>
    </AuthContainer>
  );
};

export default LoginPage;
